.contact {
  min-height: 50vh;
  padding: 4em 2em 2em;
}

.contact__form--title {
  text-align: center;
  font-size: var(--fs-h2);
  margin-bottom: 1em;
}

.contact__form--text {
  font-size: var(--fs-h3);
  margin: 2em 0;
}

.contact__form--form {
  font-size: var(--fs-h3);
  color: inherit;
  display: grid;
  grid-gap: 1.5em;
  grid-template-rows: repeat(3, 0.4fr) 2fr;
  grid-template-columns: 0.5fr 2fr;
}

.contact__label--name {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
}
.contact__input--name {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  padding: 0.8em 1em;
}
.contact__label--email {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}
.contact__input--email {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  padding: 0.8em 1em;
}
.contact__label--msg {
  grid-row: 3 / 4;
  grid-column: 1 / 3;
  justify-self: center;
}
.contact__input--msg {
  grid-row: 4 / 5;
  grid-column: 1 / 3;
  padding: 0.8em 1em;
}

.contact__form--form input,
.contact__form--form textarea {
  border: 1.5px solid var(--clr-accent);
  border-radius: 5px;
  background-color: #e7f0fe;
  color: var(--clr-dark);
  font-size: var(--fs-body);
  font-weight: var(--fw-bold);
}

.contact__form--form label {
  display: flex;
  align-items: center;
}

.contact__form--submit {
  display: block;
  padding: 10px;
  margin: 1.5em auto 0;
  border: 1px solid var(--clr-accent);
  border-radius: 15%;
  background-color: #e7f0fe;
  font-size: var(--fs-body);
  font-weight: var(--fw-bold);
  color: var(--clr-accent);
  box-shadow: 2px 2px 4px rgba(187, 187, 187, 0.45);
  cursor: not-allowed;
}

.panda {
  display: none;
}

@media (min-width: 600px) {
  .contact__form {
    width: 80%;
    margin: 0 auto;
    text-align: center;
  }

  .contact__form--form {
    width: 50%;
    margin: 0 auto 1.5em;
  }

  .contact__form--submit {
    margin-bottom: 1em;
  }

  .contact__form--submit:hover {
    background-color: var(--clr-accent);
    color: var(--clr-light);
    border: 1px solid var(--clr-light);
    cursor: not-allowed;
  }

  .panda {
    display: block;
    position: absolute;
    z-index: 10;
  }
}
