@import url(https://fonts.googleapis.com/css?family=Signika:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Tomorrow:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Charm:400,700&display=swap);
*,
*::before,
*::after {
	box-sizing: border-box;
}

:root {
	--ff-primary: "Signika", "Roboto", sans-serif;
	--ff-secondary: "Tomorrow", "Roboto", sans-serif;

	--fw-regular: 400;
	--fw-bold: 700;

	--clr-light: #fff;
	--clr-dark: rgb(5, 13, 24);
	--clr-dark-mode: rgb(33, 46, 65);
	--clr-accent: rgb(0, 31, 170);

	--fs-h1: 3rem;
	--fs-h2: 2.25rem;
	--fs-h3: 1.15rem;
	--fs-body: 1rem;

	--box-shadow: 0.25em 0.25em 0.75em rgba(170, 170, 170, 0.65),
		0.125em 0.125em 0.25em rgba(187, 187, 187, 0.65);
}

html {
	scroll-behavior: smooth;
}

@media (max-width: 390px) {
	:root {
		--fs-h1: 2.6rem;
		--fs-h2: 1.5rem;
		--fs-h3: 0.9rem;
		--fs-body: 0.75rem;
	}
}

@media (min-width: 800px) {
	:root {
		--fs-h1: 4.5rem;
		--fs-h2: 3.75rem;
		--fs-h3: 1.5rem;
		--fs-body: 1.125rem;
	}
}

.nav {
	width: 100vw;
	height: 3.5em;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	z-index: 2;
	position: fixed;
	top: 0;
	left: 0;
	min-height: 60px;
	background-color: inherit;
	color: inherit;
	box-shadow: var(--box-shadow);
}

.nav__logo {
	height: var(--fs-h2);
	padding: 5px;
}

.nav__list {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	width: 78vw;
	list-style: none;
	padding: 0;
}

.nav__link {
	color: inherit;
	text-decoration: none;
	font-size: var(--fs-h3);
}

.is-current {
	border-bottom: 4px solid  var(--clr-accent);
}

.nav__theme {
	color: inherit;
	background-color: transparent;
	border: none;
	font-size: var(--fs-h3);
	cursor: pointer;
}

.nav__theme:focus {
	outline: none;
}
.intro {
    position: relative;
    background-color: inherit;
    color: inherit;

    margin-top: 3.5em;
    padding-top: 2em;
    /* background-image: url("/images/pixcomp.jpg"); */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.intro__container {
    width: 70%;
    margin: 0 auto;
    padding-bottom: 2em;
}

.intro__title--first,
.intro__title--last {
    font-size: var(--fs-h1);
    padding-left: 0.5em;
    color: var(--clr-light);
}
.intro__subtitle {
    font-family: var(--ff-secondary);
    font-size: var(--fs-h3);
    text-transform: uppercase;
    color: var(--clr-light);
}

.intro__img {
    box-shadow: var(--box-shadow);
    border-radius: 15%;
}

@media (min-width: 800px) {
    .intro {
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .intro__container {
        display: grid;
        grid-gap: 2em;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 500px;
        min-width: 80%;
        margin: 0 auto;
    }

    .intro__titles {
        grid-column: 1 / 2;
        display: grid;
        grid-template-rows: repeat(3, 100px);
        justify-self: flex-end;
    }

    .intro__img {
        min-width: 300px;
        height: 650px;
        grid-column: 2 / 3;
        margin-top: -7em;
    }

    .intro__title--first {
        grid-row: 1 / 2;
        text-align: left;
        align-self: end;
        margin: 0;
        padding: 0;
    }
    .intro__title--last {
        grid-row: 2 / 3;
        text-align: center;
        height: -webkit-max-content;
        height: max-content;
    }

    .intro__subtitle {
        grid-row: 3 / 4;
        text-align: left;
        align-self: end;
    }
}

.specialty-card__container {
