.project-card__container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    margin: 1em auto;
}

.project-card__each {
    margin: 3em 1.5em;
    max-width: 350px;
    height: 200px;
    box-shadow: var(--box-shadow);
}

.project-card__img {
    width: 100%;
    height: 100%;
    /** how to the resize of the image should be done within its container - Cover will cut off the sides, preserve the aspect ration and fill in the space */
    object-fit: cover;                  
    overflow: hidden;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.project-card__figcaption {
    /** make the figcaption visible. If hidden, the element will still take up the space on the page */
    visibility: visible;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #1d1d1dd0;
    margin: 0 auto;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: var(--box-shadow);
}

.project-card__name {
    color: var(--clr-light);
    font-size: var(--fs-h3);
    font-weight: var(--fw-bold);
    margin: 4px auto;
    text-align: center;
    display: block;
    width: 100%;
    text-decoration: underline;
    -webkit-text-decoration-style: double;
            text-decoration-style: double;
    -webkit-text-decoration-color: var(--clr-accent);
            text-decoration-color: var(--clr-accent);
}

.project-card__sites {
    display: block;
}

.project-card__url,
.project-card__code {
    color: var(--clr-light);
    display: inline;
    margin: 15px;
}

@media (min-width: 800px) {
    .project-card__each {
        margin: 3em 1em;
        max-width: 350px;
        min-width: 230px;
        height: 200px;
        overflow: hidden;
        position: relative;
        text-align: center;
        transition: all 0.35s ease;
        border-radius: 3%;
        box-shadow: var(--box-shadow);
    }
    /** create the side shadows */
    .project-card__each:before,
    .project-card__each:after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        transition: all 0.35s ease;
        background-color: #1d1d1d;
        content: "";
        opacity: 0.85;
        z-index: 1;
    }

    /** make them triangular and set to the side */
    .project-card__each:before {
        transform: skew(45deg) translateX(-180%);
    }

    .project-card__each:after {
        transform: skew(45deg) translateX(180%);
    }

    .project-card__img {
        max-width: 100%;
        /** makes the image aligns vertically with the top of the tallest element on the line */
        vertical-align: top;
    }

    .project-card__figcaption {
        visibility: hidden;
        width: 90%;
        display: inline-block;
        background-color: transparent;
        box-shadow: none;
    }

    .project-card__each:hover .project-card__figcaption,
    .project-card__each:focus .project-card__figcaption {
        visibility: visible;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
        transform: translate(-50%, -50%) scale(1.1);
        opacity: 1;
    }

    .project-card__each:hover:before {
        transform: skew(45deg) translateX(-49.9%);
    }

    .project-card__each:hover:after {
        transform: skew(45deg) translateX(50%);
    }

    .project-card__figcaption:hover {
        transform: scale(1.5);
        opacity: 1;
    }

    .project-card__name {
        color: var(--clr-light);
        font-size: var(--fs-h3);
        font-weight: var(--fw-bold);
    }

    .project-card__url,
    .project-card__code {
        display: block;
        margin: 10px auto;
        color: var(--clr-light);
    }
}

.projects {
    padding: 4em 2em 2em;
    min-height: 100vh;
    /* background-image: url("/images/computer.png"); */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.projects__title {
    font-size: var(--fs-h2);
    text-align: center;
    margin-bottom: 1em;
    color: var(--clr-light);
}

.contact {
  min-height: 50vh;
  padding: 4em 2em 2em;
}

.contact__form--title {
  text-align: center;
  font-size: var(--fs-h2);
  margin-bottom: 1em;
}

.contact__form--text {
  font-size: var(--fs-h3);
  margin: 2em 0;
}

.contact__form--form {
  font-size: var(--fs-h3);
  color: inherit;
  display: grid;
  grid-gap: 1.5em;
  grid-template-rows: repeat(3, 0.4fr) 2fr;
  grid-template-columns: 0.5fr 2fr;
}

.contact__label--name {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
}
.contact__input--name {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  padding: 0.8em 1em;
}
.contact__label--email {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}
.contact__input--email {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  padding: 0.8em 1em;
}
.contact__label--msg {
  grid-row: 3 / 4;
  grid-column: 1 / 3;
  justify-self: center;
}
.contact__input--msg {
  grid-row: 4 / 5;
  grid-column: 1 / 3;
  padding: 0.8em 1em;
}

.contact__form--form input,
.contact__form--form textarea {
  border: 1.5px solid var(--clr-accent);
  border-radius: 5px;
  background-color: #e7f0fe;
  color: var(--clr-dark);
  font-size: var(--fs-body);
  font-weight: var(--fw-bold);
}

.contact__form--form label {
  display: flex;
  align-items: center;
}

.contact__form--submit {
  display: block;
  padding: 10px;
  margin: 1.5em auto 0;
  border: 1px solid var(--clr-accent);
  border-radius: 15%;
  background-color: #e7f0fe;
  font-size: var(--fs-body);
  font-weight: var(--fw-bold);
  color: var(--clr-accent);
  box-shadow: 2px 2px 4px rgba(187, 187, 187, 0.45);
  cursor: not-allowed;
}

.panda {
  display: none;
}

@media (min-width: 600px) {
  .contact__form {
    width: 80%;
    margin: 0 auto;
    text-align: center;
  }

  .contact__form--form {
    width: 50%;
    margin: 0 auto 1.5em;
  }

  .contact__form--submit {
    margin-bottom: 1em;
  }

  .contact__form--submit:hover {
    background-color: var(--clr-accent);
    color: var(--clr-light);
    border: 1px solid var(--clr-light);
    cursor: not-allowed;
  }

  .panda {
    display: block;
    position: absolute;
    z-index: 10;
  }
}

.panda {
  width: 400px;
  height: 550px;
  transition: all 1s;
  transform: translateX(-105%) translateY(90%);
}

.show {
  transform: rotate(35deg) translateX(30%) translateY(95%);
}

.face {
  width: 148px;
  height: 150px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  position: relative;
  left: 30%;
  top: 8%;
}

.ear {
  width: 45px;
  height: 55px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  z-index: -1;
}

.left-ear {
  left: -5px;
}

.right-ear {
  right: -5px;
}

.eye {
  position: absolute;
  background-color: black;
  top: 27%;
  left: 45%;
  width: 40px;
  height: 50px;
}

.left-eye {
  border-radius: 100% 60% 60% 60%;
  transform: translateX(-45px);
}

.right-eye {
  border-radius: 60% 100% 60% 60%;
  transform: translateX(18px);
}

.spark {
  width: 20px;
  height: 25px;
  background: #fff;
  border-radius: 50%;
  position: relative;
  top: 30%;
  left: 23%;
}

.spark-left {
  border-radius: 100% 60% 60% 60%;
}

.spark-right {
  border-radius: 60% 100% 60% 60%;
}

.dot {
  width: 10px;
  height: 10px;
  background: #000;
  border-radius: 50%;
  position: relative;
  top: 33%;
  left: 25%;
}

.nose {
  width: 0px;
  height: 0px;
  background: transparent;
  border: 25px solid transparent;
  border-top-color: #000;
  border-radius: 30px;
  position: relative;
  top: 55%;
  left: 50%;
  transform: translate(-50%);
}

.mouth {
  background: transparent;
  border-radius: 0 0 50% 50%;
  border: 2px solid transparent;
  border-bottom: 2px solid #000;
  position: relative;
  top: 13%;
  left: 50%;
  transform: translate(-50%);
  height: 50px;
  width: 50px;
}

.body {
  width: 180px;
  height: 230px;
  background: #000;
  position: relative;
  left: 27%;
  z-index: -1;
  border-radius: 50%;
}

.white {
  background: #fff;
  width: 181px;
  height: 150px;
  position: relative;
  top: 25%;
  border-radius: 100%;
}

.arm {
  width: 45px;
  height: 160px;
  background: #000;
  border-radius: 30px;
  position: absolute;
  top: -20%;
  z-index: -2;
}

.left-arm {
  left: -15%;
  transform: rotate(-25deg);
}

.right-arm {
  left: 90%;
  transform: rotate(25deg);
}

.leg {
  width: 90px;
  height: 85px;
  background: #000;
  border-radius: 30% 30% 50% 50%;
  position: absolute;
  top: 70%;
}

.left-leg {
  left: -15%;
}

.right-leg {
  left: 65%;
}

.leg-dot-left,
.leg-dot-right {
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 30% 30% 50% 50%;
  position: relative;
  top: 20%;
}

.leg-dot-left {
  left: 38%;
}

.leg-dot-left::before {
  content: "";
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 30% 30% 50% 50%;
  position: absolute;
  left: -130%;
}

.leg-dot-left::after {
  content: "";
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 30% 30% 50% 50%;
  position: absolute;
  left: 130%;
}

.leg-dot-right {
  left: 38%;
}

.leg-dot-right::before {
  content: "";
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 30% 30% 50% 50%;
  position: absolute;
  left: -130%;
}

.leg-dot-right::after {
  content: "";
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 30% 30% 50% 50%;
  position: absolute;
  left: 130%;
}
